<template>
  <div class="uploadBox">
    <div class="upload">
      <el-upload class="upload-video" drag :auto-upload="false" :limit="1" action="" :file-list="fileList"
        :on-change="ChangeFile" :on-exceed="beyondFileNumber" multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
      </el-upload>
    </div>
    <el-progress :text-inside="true" :stroke-width="18" :percentage="progress" v-if="progress !== 0">
    </el-progress>
    <div class="uploadBtn">
      <el-button @click="doUpload" class="uploadVideoBtn"></el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
const OSS = require('ali-oss')
let ossClient

export default {
  name: "uploadVideo",
  data() {
    return {
      //videoType0,1
      videoType: -1,
      fileList: [],
      ossConfig: {},
      urlLink: '',
      progress: 0
    }
  },
  methods: {
    async doUpload() {
      console.log(this.fileList);
      //
      if (this.fileList.length === 0) {
        this.$message.error('')
        return
      }

      if (ossClient === undefined) {
        let ossConfig
        await this.getStsToken().then(res => {
          console.log(res);
          if (res.status === 200) {
            let data = res.data.data
            ossConfig = {
              region: data.region,
              accessKeyId: data.accessKeyId,
              accessKeySecret: data.accessKeySecret,
              stsToken: data.stsToken,
              bucket: data.bucket
            }
            console.log(ossConfig);
          } else {
            alert('sts token get failed')
          }
        })
        ossClient = new OSS(ossConfig)
      }

      await this.doMultipartUpload()
    },
    //,
    ChangeFile(file) {
      this.fileList[0] = file
    },
    //(1)
    beyondFileNumber() {
      this.$message.error(',')
    },
    getStsToken() {
      return axios({
        method: 'get',
        url: 'http://localhost:8989/api/oss/sts'
      })
    },
    //
    async doMultipartUpload() {
      let that = this
      let key
      try {
        let file = this.fileList[0].raw
        console.log(123, file)
        if (file.type === 'image/png' || file.type === 'image/jpg') {
          this.videoType = 0
          key = '/test/userUploadVideoImg/' + file.name
        }
        key = '/test/userUploadVideoData/' + file.name
        let result = await ossClient.multipartUpload(key, file, {
          progress: function (p) {
            //(p)
            that.progress = p * 100
          },
          partsSize: 1000 * 1024 * 1024
        })

        console.log(result)
        //
        this.$message.error('')
        this.fileList = []
        this.progress = 0
        if (file.type === 'image/png' || file.type === 'image/jpg' || file.type === 'image/jpeg') {
          this.$parent.videoData.image = result.name
        } else {
          this.$parent.videoData.link = result.name
        }
        console.log(this.$parent)
      } catch (e) {
        console.log(e)
      }
    },
  },
}
</script>

<style scoped lang="less">
.upload {
  margin: 5px 10px;

  .upload-video {
    display: flex;
    flex-direction: column;
  }
}

.uploadBtn {
  display: flex;
  align-content: center;
  justify-content: center;

  .uploadVideoBtn {
    margin-top: 10px;
    width: 160px;
  }
}

:deep .el-upload-dragger {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 240px;
  height: 320px;
}
</style>

