<!---->
<template>
  <div class="MainBox w MainPageHeader">
    <div class="containBox">
      <personAside></personAside>
      <div class="contain-right">
        <div class="showBox">
          <div class="allBox">
            <personHeader></personHeader>
            <div class="uploadVideo">
              <div class="uploadLeft">
                <uploadVideo></uploadVideo>
              </div>
              <div class="uploadRight">
                <div class="uploadItem">
                  <div class="laber"></div>
                  <div class="uploadItemData">
                    <input type="text" v-model="videoData.name">
                  </div>
                </div>
                <div class="uploadItem">
                  <div class="laber"></div>
                  <div class="uploadItemData">
                    <textarea v-model="videoData.introduction"></textarea>
                  </div>
                </div>
                <div class="uploadItem">
                  <div class="laber"></div>
                  <div class="uploadItemData">
                    <el-select v-model="videoData.typeId" clearable placeholder="">
                      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                  </div>
                </div>
                <div class="uploadItem">
                  <div class="laber"></div>
                  <div class="uploadItemData">
                    <input type="text" v-model="videoData.duration">
                  </div>
                </div>
                <div class="uploadItem">
                  <div class="laber"></div>
                  <div class="uploadItemData">
                    <div>{{ videoData.image }}</div>
                  </div>
                </div>
                <div class="uploadItem">
                  <div class="laber"></div>
                  <div class="uploadItemData">
                    <div>{{ videoData.link }}</div>
                  </div>
                </div>
                <div class="uploadBtn" @click="uploadVideoAction"></div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { uploadVideoData } from '../../service/userServers'
import personAside from '../../components/personCenter/personAside'
import personHeader from '../../components/personCenter/personHeader'
import UploadVideo from "@/components/upload/uploadVideo.vue";
export default {
  name: "collectionVideo",
  data() {
    return {
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      },
      {
        value: '6',
        label: ''
      }],
      videoData: {
        typeId: '',
        name: '',
        introduction: '',
        duration: '',
        link: '',
        image: '',
      }
    }
  },

  components: {
    UploadVideo,
    personAside,
    personHeader
  },
  //vuex
  mounted() {
    this.$store.dispatch('userInfo/getUserInfo')
  },
  methods:{
    uploadVideoAction(){
      uploadVideoData(this.videoData).then((res)=>{
        console.log(res);
      })
    }
  }
};
</script>

<style lang='less' scoped>
.MainBox {
  .containBox {
    margin-top: 5px;
    display: flex;

    .contain-right {
      flex: 3;
    }
  }
}

.uploadVideo {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;

  .uploadLeft {
    overflow: hidden;
  }

  .uploadRight {
    flex: 60%;

    .uploadItem {
      margin: 10px 0;
      display: flex;
      align-items: center;
      width: 100%;

      .laber {
        font-size: 16px;
      }

      .uploadItemData {
        flex: 80%;
        margin-left: 10px;

        input,
        textarea {
          padding: 8px 5px;
          width: 100%;
          border-radius: 8px;
          border: 1px solid #DCDFE6;
        }
      }
    }

    .uploadBtn {
      float: right;
      margin-top: 36px;
      padding: 9px 20px;
      width: 100px;
      cursor: pointer;

      border: 1px solid #DCDFE6;

      text-align: center;
      font-size: 14px;
      border-radius: 6px;
    }

  }
}
</style>
